.list{
    color: antiquewhite;
    background-color: red;
    width:  300px;
    height: 50px;
    margin: 20px auto;
    border-radius: 15px;
}

.list p{
 padding: 10px;
 position: relative;
}

.list p span{
    position: absolute;
    right: 10px;
    cursor: pointer;
}
.faicon{
    margin: 0 auto;
    font-size: 15px;
}